import { useState, useRef } from "react"
import Home from "./routes/home"
import Plugins from "./routes/plugins"
import Fa from "./routes/fa"
import packageJson from "../package.json"  // eslint-disable-next-line
import { Route, Switch, Link, useLocation } from "react-router-dom"
import { IconHome, IconList } from "@tabler/icons"
import { ReactComponent as GitHub } from "./assets/github.svg"
import { ReactComponent as NPM } from "./assets/npm.svg"
import { ReactComponent as US } from "./assets/us.svg"
import { ReactComponent as IR } from "./assets/ir.svg"

import 'prismjs/components/prism-jsx.min'
import "prismjs/themes/prism-okaidia.css"

import "react-multi-date-picker/styles/backgrounds/bg-dark.css"
import "react-multi-date-picker/styles/backgrounds/bg-gray.css"
import "react-multi-date-picker/styles/backgrounds/bg-brown.css"

import "react-multi-date-picker/styles/colors/red.css"
import "react-multi-date-picker/styles/colors/green.css"
import "react-multi-date-picker/styles/colors/yellow.css"
import "react-multi-date-picker/styles/colors/purple.css"
import "react-multi-date-picker/styles/colors/teal.css"

import "react-multi-date-picker/styles/layouts/mobile.css"
import "react-multi-date-picker/styles/layouts/prime.css"

// import "./styles/colors/red.css"
// import "./styles/colors/green.css"
// import "./styles/colors/yellow.css"
// import "./styles/colors/purple.css"
// import "./styles/colors/teal.css"
// import "./styles/backgrounds/bg-dark.css"
// import "./styles/backgrounds/bg-gray.css"
// import "./styles/backgrounds/bg-brown.css"
// import "./styles/layouts/mobile.css"
// import "./styles/layouts/prime.css"

export default function App() {
  const location = useLocation()
  const [language, setLanguage] = useState(location.pathname.includes("fa") ? "fa" : "en")
  const [isActive, setIsActive] = useState(false)
  const appRef = useRef(null)

  return (
    <div ref={appRef} className={`App ${language === "fa" ? "rtl" : ""}`} >

      <nav className="navbar">
        <IconList className="open-sidebar" size={30} onClick={() => setIsActive(!isActive)} />

        <div className="navbar-title">
          <span className="name">React Multi Date Picker</span>
          <span className="version">
            {" " + packageJson.dependencies["react-multi-date-picker"].replace("^", "")}
          </span>
        </div>

        <div className="icons">

          {location.pathname.includes("plugins") ?
            <div className="icon home">
              <Link to="/react-multi-date-picker/" className="icon"><IconHome /></Link>
            </div> :
            <div className="icon language"
              onClick={() => setLanguage(language === "en" ? "fa" : "en")}
            >
              <Link
                to={language === "en" ?
                  "/react-multi-date-picker/fa" :
                  "/react-multi-date-picker/"
                }
              >
                {language === "en" ? <IR navbar-title="persian" /> : <US navbar-title="english" />}
              </Link>
            </div>
          }

          <div className="icon">
            <a
              target="_blank"
              rel="noopener noreferrer"
              className="github"
              href="https://github.com/shahabyazdi/react-multi-date-picker">
              <GitHub />
            </a>
          </div>

          <div className="icon">
            <a
              target="_blank"
              rel="noopener noreferrer"
              className="npm"
              href="https://www.npmjs.com/package/react-multi-date-picker">
              <NPM />
            </a>
          </div>

        </div>

      </nav>

      <Switch>
        <Route exact path="/"><Home isActive={isActive} setIsActive={setIsActive} /></Route>
        <Route exact path="/react-multi-date-picker"><Home isActive={isActive} setIsActive={setIsActive} /></Route>
        <Route exact path="/react-multi-date-picker/plugins" ><Plugins isActive={isActive} setIsActive={setIsActive} /></Route>
        <Route exact path="/react-multi-date-picker/fa" ><Fa isActive={isActive} setIsActive={setIsActive} /></Route>
      </Switch>

    </div>
  )
}