import React from "react"

export default class CustomComponent extends React.Component {
    render() {
        return (
            <button onClick={this.props.openCalendar}>
                {this.props.stringDate}
            </button >
        )
    }
}

export function CustomInput({ openCalendar, stringDate, handleValueChange }) {
    return (
        <input
            onFocus={openCalendar}
            value={stringDate}
            onChange={handleValueChange}
        />
    )
}

export function CustomRangeInput({ openCalendar, stringDates }) {
    let from = stringDates[0] || ""
    let to = stringDates[stringDates.length - 1] || ""
    let value = from && to ? `from: ${from}, to: ${to}` : ""

    return (
        <input
            onFocus={openCalendar}
            value={value}
            readOnly
        />
    )
}

export function CustomMultipleInput({ openCalendar, stringDates }) {
    return (
        <input
            onFocus={openCalendar}
            value={stringDates.join(", ")}
            readOnly
        />
    )
}